import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { injectStripe, Elements } from 'react-stripe-elements';
import PaymentContainer from './PaymentContainer';
import styles from './Payment.module.scss';

// TODO: remove
class Payment extends Component {
    static propTypes = {
        bot: PropTypes.object.isRequired,
        message: PropTypes.object.isRequired,
        lastUserEmail: PropTypes.string.isRequired,
        scrollToBottom: PropTypes.func.isRequired,
        showResponse: PropTypes.func.isRequired,
        parentResponseId: PropTypes.number.isRequired,
    };

    render() {
        return (
            <div className={styles.root}>
                <Elements>
                    <StripeInjector {...this.props} />
                </Elements>
            </div>
        );
    }
}

const StripeInjector = injectStripe(({ stripe, ...rest }) => stripe && <PaymentContainer stripe={stripe} {...rest} />);

export default Payment;
