import React from 'react';
import PropTypes from 'prop-types';
import { FiRefreshCcw } from 'react-icons/fi';

import { __t } from '@mssgme/shared';
import logoSrc from '../../../img/logo_bot.svg';
import styles from './Header.module.scss';

const Header = ({ resetBot }) => (
    <div className={styles.root}>
        <a href="/" target="_blank" rel="noopener noreferrer" className={styles.headerLink}>
            <div className={styles.logo}>
                <img src={logoSrc} alt="mssg.me logo" />
            </div>
            <div className={styles.info}>
                <h1 className={styles.botName}>Mia</h1>
                <p className={styles.copyright}>{__t('copyright')}</p>
            </div>
        </a>
        <div className={styles.resetButton} onClick={resetBot} title="Restart bot">
            <FiRefreshCcw size={24} color="#fff" />
        </div>
    </div>
);

Header.propTypes = {
    resetBot: PropTypes.func.isRequired,
};

export default Header;
