import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Response from './Response';
import styles from './Dialog.module.scss';

class Dialog extends Component {
    static propTypes = {
        bot: PropTypes.object.isRequired,
        card: PropTypes.object.isRequired,
        dialog: PropTypes.array.isRequired,
        lastUserEmail: PropTypes.string.isRequired,
        showResponse: PropTypes.func.isRequired,
        setUserEmail: PropTypes.func.isRequired,
        hasBackButton: PropTypes.bool.isRequired,
        goBack: PropTypes.func.isRequired,
    };

    DialogScrollBox = React.createRef();

    componentDidMount() {
        const {
            bot: {
                botData: { startResponseId },
            },
            showResponse,
        } = this.props;

        showResponse(startResponseId);
    }

    scrollToBottom = () => {
        const scrollableBox = this.DialogScrollBox.current;

        setTimeout(() => {
            scrollableBox.scroll({
                top: scrollableBox.scrollHeight,
                left: 0,
                behavior: 'smooth',
            });
        }, 10);
    };

    render() {
        const { card, setUserEmail, showResponse, dialog, lastUserEmail, hasBackButton, goBack, bot } = this.props;

        return (
            <div className={styles.root}>
                <div className={styles.content} ref={this.DialogScrollBox} onScroll={this.handleScroll}>
                    <div className={styles.responsesContainer}>
                        {dialog &&
                            dialog.map((response) => {
                                return (
                                    <Response
                                        card={card}
                                        key={response.uid}
                                        bot={bot}
                                        response={response}
                                        showResponse={showResponse}
                                        scrollToBottom={this.scrollToBottom}
                                        stashUserEmail={setUserEmail}
                                        lastUserEmail={lastUserEmail}
                                        hasBackButton={hasBackButton}
                                        goBack={goBack}
                                    />
                                );
                            })}
                    </div>
                </div>
            </div>
        );
    }
}

export default Dialog;
