import React, { Component } from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import { FiArrowLeft } from 'react-icons/fi';

import Message from './Message';
import LoaderTyping from '../../UI/LoaderTyping';
import logoSrc from '../../../../img/logo_bot.svg';
import styles from './Response.module.scss';

const BotPropTypes = {
    response: PropTypes.shape({
        id: PropTypes.number.isRequired,
        uid: PropTypes.number.isRequired,
        messages: PropTypes.array.isRequired,
    }),
};

const UserPropTypes = {
    response: PropTypes.shape({
        uid: PropTypes.number.isRequired,
        content: PropTypes.string.isRequired,
    }),
};

class Response extends Component {
    static propTypes = {
        response: PropTypes.oneOfType([BotPropTypes.response, UserPropTypes.response]).isRequired,
        showResponse: PropTypes.func.isRequired,
        typingDelay: PropTypes.number,
        scrollToBottom: PropTypes.func.isRequired,
        card: PropTypes.object.isRequired,
        bot: PropTypes.object.isRequired,
        stashUserEmail: PropTypes.func.isRequired,
        lastUserEmail: PropTypes.string.isRequired,
        hasBackButton: PropTypes.bool.isRequired,
        goBack: PropTypes.func.isRequired,
    };

    static defaultProps = {
        typingDelay: 1000,
    };

    state = {
        responseId: null,
        progress: 0,
        typing: true,
    };

    componentDidMount() {
        const { response } = this.props;

        if (response.type !== 'user_response') {
            this.typeNextMessage();
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        const { scrollToBottom } = this.props;

        scrollToBottom();
    }

    typeNextMessage = () => {
        const { response, typingDelay } = this.props;
        let { progress, typing } = this.state;

        setTimeout(() => {
            progress++;

            if (progress >= response.messages.length) {
                typing = false;
            }

            this.setState(
                {
                    progress,
                    typing,
                },
                () => {
                    if (typing) {
                        this.typeNextMessage();
                    }
                }
            );
        }, typingDelay);
    };

    render() {
        const {
            response,
            showResponse,
            scrollToBottom,
            card,
            bot,
            stashUserEmail,
            lastUserEmail,
            hasBackButton,
            goBack,
        } = this.props;
        const { typing, progress } = this.state;

        if (response.type !== 'user_response') {
            const currentMessages = typing ? response.messages.slice(0, progress) : response.messages;

            return (
                <div className={styles.root}>
                    <div className={styles.simpleResponseRoot}>
                        <div className={styles.avatarContainer}>
                            <div className={styles.avatarAction}>
                                <div className={cn(styles.avatar, { [styles.hidden]: hasBackButton && !typing })}>
                                    <img src={logoSrc} alt="mssg.me logo" />
                                </div>
                                <div
                                    className={cn(styles.backButton, { [styles.hidden]: !hasBackButton || typing })}
                                    onClick={goBack}
                                >
                                    <FiArrowLeft size={18} color="#333" />
                                </div>
                            </div>
                        </div>
                        <div className={styles.messagesContainer}>
                            {currentMessages.map((message) => {
                                if (!['CHOICE', 'INPUT', 'PAYMENT', 'SUBSCRIBE'].includes(message.type)) {
                                    return (
                                        <Message
                                            key={message.id}
                                            message={message}
                                            scrollToBottom={scrollToBottom}
                                            showResponse={showResponse}
                                            card={card}
                                            bot={bot}
                                            stashUserEmail={stashUserEmail}
                                            parentResponseId={response.id}
                                            lastUserEmail={lastUserEmail}
                                        />
                                    );
                                }
                            })}
                            {typing && <LoaderTyping />}
                        </div>
                    </div>
                    {currentMessages.map((message) => {
                        if (['CHOICE', 'INPUT', 'PAYMENT', 'SUBSCRIBE'].includes(message.type)) {
                            return (
                                <div className={styles.actionsBlock} key={message.id}>
                                    <div className={styles.actionsMessages} style={{ paddingLeft: '8px' }}>
                                        <Message
                                            key={message.id}
                                            bot={bot}
                                            message={message}
                                            scrollToBottom={scrollToBottom}
                                            showResponse={showResponse}
                                            card={card}
                                            stashUserEmail={stashUserEmail}
                                            parentResponseId={response.id}
                                            lastUserEmail={lastUserEmail}
                                        />
                                    </div>
                                </div>
                            );
                        }
                    })}
                </div>
            );
        } else {
            return (
                <div className={styles.userMessage}>
                    <div className={styles.userMessageBlob}>{response.content}</div>
                </div>
            );
        }
    }
}

export default Response;
