import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { StripeProvider } from 'react-stripe-elements';

import config from 'config';
import Background from './Background';
import Dialog from './Dialog';
import styles from './ChatBot.module.scss';
import Header from './Header';
import Rocket from './UI/Rocket';

class ChatBot extends Component {
    static propTypes = {
        bot: PropTypes.object.isRequired,
        card: PropTypes.object.isRequired,
    };

    state = {
        stripe: null,
        dialog: [],
        lastUserEmail: '',
        messageId: 0,
        dialogSnapshots: [],
    };

    componentDidMount() {
        if (window.Stripe) {
            this.initStripe();
        } else {
            document.querySelector('#stripe-js').addEventListener('load', () => {
                this.initStripe();
            });
        }
    }

    initStripe() {
        this.setState({
            stripe: window.Stripe(config.stripe_public_key),
        });
    }

    resetBot = () => {
        this.setState(
            {
                dialog: [],
                dialogSnapshots: [],
            },
            () => {
                this.showResponse(this.props.bot.botData.startResponseId);
            }
        );
    };

    setUserEmail = (lastUserEmail) => {
        this.setState({
            lastUserEmail,
        });
    };

    goBack = () => {
        const { dialogSnapshots } = this.state;
        const updatedDialogSnapshots = dialogSnapshots.slice(1);

        if (dialogSnapshots.length > 1) {
            this.setState({
                dialog: updatedDialogSnapshots[0],
                dialogSnapshots: updatedDialogSnapshots,
            });
        }
    };

    showResponse = (responseId, parentResponseId, choiceLabel) => {
        const {
            bot: {
                botData: { responses },
            },
        } = this.props;
        const { dialog, messageId, dialogSnapshots } = this.state;
        const nextResponse = responses.find((response) => response.id === responseId);
        const currentDialog = dialog;
        let nextId = messageId + 1;

        if (parentResponseId) {
            const updatedDialog = [
                ...dialog.map((response) => {
                    if (response.id === parentResponseId) {
                        return {
                            ...response,
                            messages: response.messages.filter(
                                (message) =>
                                    message.type !== 'CHOICE' &&
                                    message.type !== 'SUBSCRIBE' &&
                                    message.type !== 'PAYMENT'
                            ),
                        };
                    }

                    return response;
                }),
                {
                    uid: nextId++,
                    type: 'user_response',
                    content: choiceLabel,
                },
                { ...nextResponse, uid: nextId },
            ];

            this.setState({
                dialog: [...updatedDialog],
                messageId: nextId,
                dialogSnapshots: [updatedDialog, ...dialogSnapshots],
            });
        } else {
            const updatedDialog = [...currentDialog, { ...nextResponse, uid: nextId }];

            this.setState({
                dialog: updatedDialog,
                dialogSnapshots: [updatedDialog, ...dialogSnapshots],
                messageId: nextId,
            });
        }
    };

    render() {
        const { bot, card } = this.props;
        const { dialog, lastUserEmail, dialogSnapshots } = this.state;

        return (
            <div className={styles.root}>
                <Background />
                <Rocket />
                <div className={styles.pane}>
                    <Header resetBot={this.resetBot} />
                    <StripeProvider stripe={this.state.stripe}>
                        <Dialog
                            bot={bot}
                            dialog={dialog}
                            card={card}
                            lastUserEmail={lastUserEmail}
                            showResponse={this.showResponse}
                            setUserEmail={this.setUserEmail}
                            hasBackButton={dialogSnapshots.length > 1}
                            goBack={this.goBack}
                        />
                    </StripeProvider>
                </div>
            </div>
        );
    }
}

export default ChatBot;
