import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { CardElement } from 'react-stripe-elements';
import { FaSpinner } from 'react-icons/fa';

import { botApi } from '@mssgme/lib';
import { __t } from '@mssgme/shared';
import styles from './PaymentContainer.module.scss';

const stripeCardElementOptions = {
    style: {
        base: {
            fontSize: '16px',
            lineHeight: '16px',
            color: '#000',
            fontFamily: 'Comfortaa, sans-serif',
            letterSpacing: '0px',
            '::placeholder': {
                color: '#999',
            },
        },
        invalid: {
            color: '#c23d4b',
        },
    },
};

class PaymentContainer extends Component {
    static propTypes = {
        bot: PropTypes.object.isRequired,
        message: PropTypes.object.isRequired,
        stripe: PropTypes.object.isRequired,
        lastUserEmail: PropTypes.string.isRequired,
        scrollToBottom: PropTypes.func.isRequired,
        showResponse: PropTypes.func.isRequired,
        parentResponseId: PropTypes.number.isRequired,
    };

    state = {
        canMakePayment: false,
        replaceButtonsWithForm: false,
        buttonForm: {
            visible: false,
        },
        lastUserEmail: this.props.lastUserEmail,
        isFetching: false,
    };

    buildPaymentRequest = async ({ description, amount }) => {
        const { stripe } = this.props;
        const currency = 'usd';
        const paymentRequest = stripe.paymentRequest({
            country: 'US',
            currency,
            total: {
                label: description,
                amount: amount,
            },

            requestPayerName: true,
            requestPayerEmail: true,
        });

        paymentRequest.on('token', async ({ token, complete }) => {
            const success = await this.chargeDonation(token, { amount, currency, description });

            if (success) {
                complete('success');
            } else {
                complete('fail');
            }
        });

        return paymentRequest.canMakePayment().then((result) => ({
            paymentRequest,
            canMakePayment: !!result,
        }));
    };

    handlePayClick = ({ provider, description, amount }) => async () => {
        if (provider === 'stripe') {
            const { paymentRequest, canMakePayment } = await this.buildPaymentRequest({ description, amount });

            if (canMakePayment) {
                this.setState({
                    canMakePayment,
                });
                paymentRequest.show();
            } else {
                this.setState({
                    buttonForm: {
                        visible: true,
                        amount,
                        description,
                    },
                });
                this.props.scrollToBottom();
            }
        }
    };

    chargeDonation = async (token, payload) => {
        const { message, parentResponseId, bot } = this.props;

        try {
            await botApi.donate(bot._id, {
                token,
                ...payload,
            });

            this.setState({
                error: null,
                isFetching: false,
            });
            this.props.showResponse(message.responseId, parentResponseId, `$${payload.amount / 100}`);
            this.props.scrollToBottom();
        } catch (error) {
            this.setState({
                isFetching: false,
                error: error.errors,
            });

            return false;
        }

        return true;
    };

    handleStripeFormSubmit = ({ amount, description }) => async (event) => {
        event.preventDefault();

        this.setState({
            isFetching: true,
        });

        const { stripe } = this.props;
        const { token, error } = await stripe.createToken({ name: 'Bot User' });
        const currency = 'usd';

        if (error) {
            this.setState({
                error,
                isFetching: false,
            });
            this.props.scrollToBottom();
        } else {
            await this.chargeDonation(token, { amount, currency, description });
        }
    };

    render() {
        const { buttonForm, error, isFetching } = this.state;
        const { message } = this.props;

        return (
            <>
                {!buttonForm.visible && (
                    <>
                        <div className={styles.buttonsContainer}>
                            {message.buttons.map(({ id, provider, title, description, amount }) => (
                                <div
                                    key={id}
                                    className={styles.payButton}
                                    onClick={this.handlePayClick({ provider, description, amount })}
                                >
                                    {title}
                                </div>
                            ))}
                        </div>
                        {error && !buttonForm.visible && (
                            <span className={styles.error} style={{ marginTop: '16px' }}>
                                {error.message}
                            </span>
                        )}
                    </>
                )}
                {buttonForm.visible && (
                    <div className={styles.formContainer}>
                        <div className={styles.cardInputContainer}>
                            <CardElement hidePostalCode {...stripeCardElementOptions} className={styles.cardInput} />
                        </div>
                        {error && <span className={styles.error}>{error.message}</span>}
                        <span
                            className={cn(styles.submitButton, { [styles.disabled]: isFetching })}
                            onClick={this.handleStripeFormSubmit(buttonForm)}
                        >
                            {isFetching && (
                                <>
                                    <FaSpinner className={styles.spinner} size={20} color="#333" />
                                    {' Sending...'}
                                </>
                            )}
                            {!isFetching && `${__t('send')} $${buttonForm.amount / 100}`}
                        </span>
                    </div>
                )}
            </>
        );
    }
}

export default PaymentContainer;
