import React, { Component } from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import styles from './Messengers.module.scss';
import { FaTelegramPlane, FaFacebookMessenger, FaViber, FaVk, FaWhatsapp, FaSkype } from 'react-icons/fa';

const iconsMappig = {
    telegram: FaTelegramPlane,
    viber: FaViber,
    messenger: FaFacebookMessenger,
    vkontakte: FaVk,
    whatsapp: FaWhatsapp,
    skype: FaSkype,
};

class Messengers extends Component {
    static propTypes = {
        message: PropTypes.object.isRequired,
    };

    render() {
        const {
            message: { messengers },
        } = this.props;

        return (
            <div className={styles.root}>
                {messengers.map(({ type, url }) => {
                    const Icon = iconsMappig[type];

                    return (
                        <a
                            key={type}
                            href={url}
                            target="_blank"
                            rel="noopener noreferrer"
                            className={cn(styles.button, styles[type])}
                        >
                            <Icon color="#fff" size="20" />
                        </a>
                    );
                })}
            </div>
        );
    }
}

export default Messengers;
