import '@mssgme/scss-basics';
import React from 'react';
import smoothscroll from 'smoothscroll-polyfill';
import { render } from 'react-dom';

import { trans } from '@mssgme/shared';
import ChatBot from './components/ChatBot';

smoothscroll.polyfill();

const language = window.document.documentElement.lang || 'en';

trans.extend(window.LOCALES);

render(<ChatBot bot={window.BOT_DATA} card={window.CARD_DATA} lang={language} />, document.getElementById('root'));
