import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Choices from './Choices';
import Subscribe from './Subscribe';
import Link from './Link';
import Messengers from './Messengers';
import Payment from './Payment';
import Products from './Products';
import Share from './Share';
import styles from './Message.module.scss';
import Text from './Text';
import Image from './Image';
import Video from './Video';
import cn from 'classnames';

const typeMapping = {
    TEXT: Text,
    GIF: Image,
    IMAGE: Image,
    VIDEO: Video,
    SUBSCRIBE: Subscribe,
    CHOICE: Choices,
    SHARE: Share,
    LINK: Link,
    MESSENGERS: Messengers,
    PRODUCTS: Products,
    PAYMENT: Payment,
};

class Message extends Component {
    static propTypes = {
        message: PropTypes.object.isRequired,
        scrollToBottom: PropTypes.func.isRequired,
        responseId: PropTypes.number,
        card: PropTypes.object.isRequired,
        bot: PropTypes.object.isRequired,
        showResponse: PropTypes.func,
        stashUserEmail: PropTypes.func,
        parentResponseId: PropTypes.number,
        lastUserEmail: PropTypes.string.isRequired,
    };

    render() {
        const { message } = this.props;
        const MessageComponent = typeMapping[message.type];
        const classNames = cn({
            [styles.root]: true,
            [styles.revealRight]: ['CHOICE', 'SUBSCRIBE', 'PAYMENT', 'PRODUCTS', 'MESSENGERS', 'SHARE'].includes(
                message.type
            ),
        });

        return (
            <div className={classNames}>
                <MessageComponent {...this.props} />
            </div>
        );
    }
}

export default Message;
