import React, { Component } from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import styles from './Share.module.scss';
import { FiFacebook, FiTwitter } from 'react-icons/fi';
import { FaWhatsapp } from 'react-icons/fa';

const iconsMappig = {
    facebook: FiFacebook,
    twitter: FiTwitter,
    whatsapp: FaWhatsapp,
};

class Share extends Component {
    static propTypes = {
        message: PropTypes.object.isRequired,
    };

    render() {
        const {
            message: { url, socials },
        } = this.props;
        const encodedLink = encodeURIComponent(url);
        const shareLinks = {
            twitter: `https://twitter.com/intent/tweet?url=${encodedLink}`,
            facebook: `https://www.facebook.com/sharer/sharer.php?u=${encodedLink}`,
            whatsapp: `https://api.whatsapp.com/send?text=${encodedLink}`,
        };

        return (
            <div className={styles.root}>
                {socials.map(({ type }) => {
                    const Icon = iconsMappig[type];

                    return (
                        <a
                            key={type}
                            href={shareLinks[type]}
                            target="_blank"
                            rel="noopener noreferrer"
                            className={cn(styles.button, styles[type])}
                        >
                            <Icon color="#fff" size="20" />
                        </a>
                    );
                })}
            </div>
        );
    }
}

export default Share;
