import React from 'react';
import PropTypes from 'prop-types';

import { Button } from '@mssgme/ui';
import Buttons from '../Buttons';

const Choices = ({ message, showResponse, parentResponseId }) => (
    <Buttons key={message.id}>
        {message.choices.map((choice) => {
            return (
                <Button key={choice.id} onClick={() => showResponse(choice.responseId, parentResponseId, choice.label)}>
                    {choice.label}
                </Button>
            );
        })}
    </Buttons>
);

Choices.propTypes = {
    message: PropTypes.object.isRequired,
    showResponse: PropTypes.func.isRequired,
    parentResponseId: PropTypes.number.isRequired,
};

export default Choices;
