import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { FiChevronRight } from 'react-icons/fi';
import { FaSpinner } from 'react-icons/fa';

import { admin_url } from 'config';
import { __t } from '@mssgme/shared';
import Text from '../Text';
import styles from './Subscribe.module.scss';

class Subscribe extends Component {
    static propTypes = {
        message: PropTypes.object.isRequired,
        parentResponseId: PropTypes.number.isRequired,
        scrollToBottom: PropTypes.func.isRequired,
        showResponse: PropTypes.func.isRequired,
        lastUserEmail: PropTypes.string.isRequired,
    };

    state = {
        value: this.props.lastUserEmail,
        errors: [],
        isFetching: false,
    };

    Input = React.createRef();

    handleChange = ({ target: { value } }) => {
        this.setState({
            value,
        });
    };

    handleSubmit = async (event, responseId, parentResponseId) => {
        event.preventDefault();

        this.setState({
            isFetching: true,
        });

        const { showResponse } = this.props;
        const { value } = this.state;

        const response = await fetch(`${admin_url}/api/bot/subscribe`, {
            method: 'POST',
            headers: {
                'Content-type': 'application/json',
            },
            body: JSON.stringify({
                email: value,
            }),
        });

        if (response.ok) {
            return showResponse(responseId, parentResponseId, value);
        } else {
            const responseJSON = await response.json();

            this.setState({
                errors: responseJSON.errors || [responseJSON.error],
            });
        }

        this.setState({
            isFetching: false,
        });
    };

    componentDidMount() {
        this.Input.current.focus();
        this.props.scrollToBottom();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        const { scrollToBottom } = this.props;

        scrollToBottom();
        this.Input.current.focus();
    }

    render() {
        const { message, parentResponseId } = this.props;
        const { value, errors, isFetching } = this.state;

        return (
            <div className={styles.root}>
                <form
                    className={styles.form}
                    onSubmit={(event) => this.handleSubmit(event, message.responseId, parentResponseId)}
                >
                    <input
                        ref={this.Input}
                        className={styles.input}
                        type="email"
                        placeholder={message.placeholder}
                        name={`email_${message.id}`}
                        value={value}
                        onChange={this.handleChange}
                        disabled={isFetching}
                    />
                    <button type="submit" className={styles.submit} disabled={isFetching}>
                        {!isFetching && <FiChevronRight size={20} color="#fff" />}
                        {isFetching && <FaSpinner className={styles.spinner} size={20} color="#fff" />}
                    </button>
                </form>
                {errors.length > 0 &&
                    errors.map((error) => {
                        return (
                            <div key={`sbscrb_err_${error.index}`} className={styles.error}>
                                <Text
                                    message={{
                                        content:
                                            error.param && error.param === 'email'
                                                ? __t('email_novalid')
                                                : error.message
                                                    ? error.message
                                                    : 'Unknown error, try later',
                                    }}
                                />
                            </div>
                        );
                    })}
            </div>
        );
    }
}

export default Subscribe;
